// import { Print } from '@mui/icons-material'
import { Box, Grid, Typography } from '@mui/material'
import React from 'react'
import Barcode from 'react-barcode/lib/react-barcode'
import spin2win from '../assets/spin2win.png'
import { padNumbers } from './utils'
// import { mainColor } from '../themes/colors'

const PrintableTicket = ({ ticket, ref }) => {
   
    const getNameFromTicket = (t) => {
        switch (t.type) {
            case 'EXACT':
                return `Number - ${t.number}`
            case 'RED':
                return 'Colours - Red'

            case 'GREEN':
                return 'Colours - Green'

            case 'BLACK':
                return 'Colours - Black'
            case 'DOZEN_ONE':
                return 'Dozens - 1~12'
            case 'DOZEN_TWO':
                return 'Dozens - 13~24'
            case 'DOZEN_THREE':
                return 'Dozens - 25~36'
            case 'EVEN':
                return 'Even'
            case 'ODD':
                return 'Odd'
            case 'LOW':
                return 'Low'
            case 'HIGH':
                return 'High'
            case 'SECTORA':
                return 'Sectors - A'

            case 'SECTORB':
                return 'Sectors - B'

            case 'SECTORC':
                return 'Sectors - C'

            case 'SECTORD':
                return 'Sectors - D'

            case 'SECTORE':
                return 'Sectors - E'

            case 'SECTORF':
                return 'Sectors - F'
            case 'FINALS_ZERO':
                return 'Finals - 0'

            case 'FINALS_ONE':
                return 'Finals - 1'

            case 'FINALS_TWO':
                return 'Finals - 2'

            case 'FINALS_THREE':
                return 'Finals - 3'

            case 'FINALS_FOUR':
                return 'Finals - 4'

            case 'FINALS_FIVE':
                return 'Finals - 5'

            case 'FINALS_SIX':
                return 'Finals - 6'
            case 'MIRROR_ONE':
                return 'Mirrors - 12 & 21'
            case 'MIRROR_TWO':
                return 'Mirrors - 12 & 21'
            case 'MIRROR_THREE':
                return 'Mirrors - 12 & 21'
            case 'TWINS':
                return 'Twins - 11, 22, 33'
            case 'LOW_RED':
                return 'Low & Red'

            case 'LOW_BLACK':
                return 'Low & Black'

            case 'HIGH_RED':
                return 'High & Red'

            case 'HIGH_BLACK':
                return 'High & Black'
            default:
                return `${t.type}`
        }

    }
    return (

        ticket !== null ? <Box ref={ref} sx={{ width: 310, m: 1, backgroundColor: 'white', }}>
            <Box sx={{ width: 250, height: 65, border: '1px solid black', px: 1, py: 0.5 }}>
                <Grid container justifyContent='space-between' alignItems='center'>
                    <Grid item>
                        <Typography sx={{ fontSize: 10, fontWeight: 'bold' }}>Ticket ID</Typography>
                    </Grid>

                    <Grid item>
                        <Typography sx={{ fontSize: 9 }}>{padNumbers(ticket.id, 9)}</Typography>
                    </Grid>
                </Grid>

                <Grid container justifyContent='space-between' alignItems='center'>
                    <Grid item>
                        <Typography sx={{ fontSize: 10, fontWeight: 'bold' }}>Entity ID</Typography>
                    </Grid>

                    <Grid item>
                        <Typography sx={{ fontSize: 9 }}>{ticket.nextGameGameId}</Typography>
                    </Grid>
                </Grid>

                <Grid container justifyContent='space-between' alignItems='center'>
                    <Grid item>
                        <Typography sx={{ fontSize: 10, fontWeight: 'bold' }}>Printed At</Typography>
                    </Grid>

                    <Grid item>
                        <Typography sx={{ fontSize: 9 }}>{(new Date(ticket.createdAt)).toLocaleString()}</Typography>
                    </Grid>
                </Grid>
            </Box>
            <Grid container sx={{ mt: 1 }} alignItems='start'>
                <Grid item>
                    <img width={56} height={55} src={spin2win} alt="Spin 2 Win" />
                </Grid>

                <Grid item>
                    <Grid container direction='column'>
                        <Grid item>
                            <Typography sx={{ fontSize: 11, fontWeight: 'bold' }}>Spin 2 Win</Typography>
                        </Grid>

                        <Grid item>
                            <Typography sx={{ fontSize: 9 }}></Typography>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>

            <Box sx={{ width: 250, px: 1, py: 0.5 }}>
                {/* <Grid container justifyContent='space-between' alignItems='center'>
                    <Grid item>
                        <Typography sx={{ fontSize: 12, fontWeight: 'bold' }}>Number - 23 : x36</Typography>
                    </Grid>

                    <Grid item>
                        <Typography sx={{ fontSize: 11 }}> 5</Typography>
                    </Grid>
                </Grid>

                <Grid container justifyContent='space-between' alignItems='center'>
                    <Grid item>
                        <Typography sx={{ fontSize: 12, fontWeight: 'bold' }}>Number - 23 : x36</Typography>
                    </Grid>

                    <Grid item>
                        <Typography sx={{ fontSize: 11 }}> 5</Typography>
                    </Grid>
                </Grid>
                <Grid container justifyContent='space-between' alignItems='center'>
                    <Grid item>
                        <Typography sx={{ fontSize: 12, fontWeight: 'bold' }}>Number - 23 : x36</Typography>
                    </Grid>

                    <Grid item>
                        <Typography sx={{ fontSize: 11 }}> 5</Typography>
                    </Grid>
                </Grid> */}


                <Grid sx={{ px: 1, py: 1 }} container direction='column' justifyContent='space-between' alignItems='center'>
                    {
                        ticket.value && JSON.parse(ticket.value).map((t, index) => {
                            return <Grid container direction='row' justifyContent='space-between'>
                                <Grid item lg={10}>
                                    <Typography sx={{ fontSize: 9, textAlign: 'left', fontWeight: 'bold' }}>{getNameFromTicket(t)} :   X{t.odd}</Typography>
                                </Grid>

                                <Grid item lg={2}>
                                    <Typography sx={{ fontSize: 9, textAlign: 'left', fontWeight: 'bold' }}> {t.money}</Typography>
                                </Grid>
                            </Grid>
                        })
                    }
                </Grid>

            </Box>

            {/* Total Stake */}
            <Grid container justifyContent='space-between' alignItems='center' sx={{ width: 250, px: 1, py: 0.5, borderLeft: '1px solid black', borderRight: '1px solid black' }}>
                <Grid item>
                    <Typography sx={{ fontSize: 10, fontWeight: 'bold' }}>Total Stake</Typography>
                </Grid>

                <Grid item>
                    <Typography sx={{ fontSize: 9 }}> {ticket.money}</Typography>
                </Grid>
            </Grid>

            {/* Total Info */}
            <Box sx={{ width: 250, height: 65, border: '1px solid black', px: 1, py: 0.5 }}>
                <Grid container justifyContent='space-between' alignItems='center'>
                    <Grid item>
                        <Typography sx={{ fontSize: 10, fontWeight: 'bold' }}>Max. payout per ticket</Typography>
                    </Grid>

                    <Grid item>
                        <Typography sx={{ fontSize: 9 }}> {ticket.maxPayout}</Typography>
                    </Grid>
                </Grid>

                <Grid container justifyContent='space-between' alignItems='center'>
                    <Grid item>
                        <Typography sx={{ fontSize: 10, fontWeight: 'bold' }}>Max. payout per bet</Typography>
                    </Grid>

                    <Grid item>
                        <Typography sx={{ fontSize: 9 }}> 100,000</Typography>
                    </Grid>
                </Grid>

                <Grid container justifyContent='space-between' alignItems='center'>
                    <Grid item>
                        <Typography sx={{ fontSize: 10, fontWeight: 'bold' }}>Maximum winning payout</Typography>
                    </Grid>

                    <Grid item>
                        <Typography sx={{ fontSize: 9 }}> {ticket.maxPayout}</Typography>
                    </Grid>
                </Grid>
                {/* Bar Code */}
            </Box>

            <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: 250, my: 1 }}>
                <Barcode height={50} displayValue={false} value={padNumbers(ticket.id, 9)} />
            </Box>

        </Box> : <Box><Typography>Please Wait</Typography></Box>
    )
}
export default PrintableTicket