import { Check, Close } from '@mui/icons-material'
import { Box, Divider, Grid, IconButton, Typography } from '@mui/material'
import { useSnackbar } from 'notistack'
import React, { useEffect, useRef, useState } from 'react'
import { deleteTicket, getTicketById, payTicket } from '../controllers/spinner_ticket'
import { mainColor } from '../themes/colors'
import PrintableTicket from './PrintableTicket'

const CheckTicket = () => {

    const getMaxWinning = (tkt) => {
        let maxWinning = 0
        for (const t of tkt) {
            maxWinning = Math.max(maxWinning, (t.odd * t.money))
        }
        return maxWinning
    }

    const [loading, setLoading] = useState(false)
    const [paying, setPaying] = useState()
    const [cancelling, setCancelling] = useState()
    const { enqueueSnackbar } = useSnackbar()
    const [ticket, setTicket] = useState()
    const inputRef = useRef()
    useEffect(() => {
        inputRef.current.focus()
        return () => {

        }
    }, [])

    const loadTicket = async (id) => {
        setLoading(true)
        setTicket(null)
        console.log('enter pressed')
        getTicketById(id).then(res => {
            setTicket(res && res.ticket)
            setLoading(false)
        }).catch(err => {
            enqueueSnackbar('Cannot find ticket.', { variant: 'error' })
            setLoading(false)
        })
    }

    const checkPayTicket = async (id) => {
        setPaying(true)
        payTicket(id).then(res => {
            loadTicket(id)
            setPaying(false)
            enqueueSnackbar('Ticket Paid Successfully.', { variant: 'success' })
        }).catch(err => {
            enqueueSnackbar('Cannot pay ticket please try again.', { variant: 'error' })
            setPaying(false)
        })
    }

    const checkCancelTicket = async (id) => {

        setCancelling(true)
        deleteTicket(id).then(res => {
            setCancelling(false)
            enqueueSnackbar('Ticket Cancelled Successfully.', { variant: 'success' })
            setTicket(null)
        }).catch(err => {
            console.log(err)
            enqueueSnackbar('Cannot cancel ticket please try again.', { variant: 'error' })
            setCancelling(false)
        })
    }

    return (
        <Box sx={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            width: 700,
            bgcolor: 'background.paper',
            borderRadius: 1,
            boxShadow: 24,
            p: 4,
            maxHeight: '100vh',
            overflowY: 'auto',
            margin: '15px'
        }}>
            <Typography sx={{ my: 2, fontSize: 15 }}>Check Ticket Here: </Typography>
            <input ref={inputRef} onKeyUp={async (e) => {
                if (e.code === 'Enter') {
                    if (e.target.value.length > 0) {
                        loadTicket(e.target.value)
                    }
                    else {
                        enqueueSnackbar('Please enter ticket ID.', { variant: 'warning' })
                    }
                }
            }} style={{ outline: 'none', padding: 10, border: `1px solid ${mainColor}`, borderRadius: 5, width: '100%', fontSize: 26 }} type="text" placeholder='Ticket ID' />

            {
                ticket && <Box>
                    <Typography sx={{ fontSize: 17, fontWeight: 'bold', mt: 2, color: ticket.status === 'WON' ? 'green' : ticket.status === 'LOST' ? 'red' : 'black' }}>Status : {ticket.status || 'Open'}</Typography>
                    {(ticket.status === 'WON' || ticket.status === 'PAID') && <Typography sx={{ fontSize: 17, fontWeight: 'bold', }}>Payable : {ticket.payable} </Typography>}
                </Box>
            }
            {
                ticket && <Box sx={{ my: 2 }}>
                    <PrintableTicket ticket={{ ...ticket, nextGameGameId: ticket.game_id, maxPayout: getMaxWinning(JSON.parse(ticket.value)) }} />
                </Box>
            }
            <Divider sx={{ my: 2 }} />

            {
                ticket && !ticket.status && <Grid container justifyContent='space-between' alignItems='center' sx={{ width: 340, mt: 3, mb: 4, ml: 3 }}>
                    <Grid item>
                        <Typography sx={{ fontSize: 25 }}>Cancel Ticket?</Typography>
                    </Grid>

                    <Grid item>
                        <IconButton sx={{ p: 1, backgroundColor: '#F5F5F5', mr: 2 }}><Check onClick={() => {
                            checkCancelTicket(ticket && ticket.id)
                        }} sx={{ color: 'green', fontSize: 32 }} /></IconButton>
                        <IconButton sx={{ p: 1, backgroundColor: '#F5F5F5', }}><Close sx={{ color: 'red', fontSize: 32, }} /></IconButton>
                    </Grid>
                </Grid>
            }

        </Box >
    )
}

export default CheckTicket